<script>
import axios from "axios";
import moment from "moment";

export default {
  data() {
    return {
      search: "",
      boards: [],
      pageNum: 0
    };
  },
  created() {
    axios.get("http://localhost:9999/pjt/api/board").then(({ data }) => {
      console.log(data);
      this.boards = data;
    });
  },
  props: {
    pageSize: {
      type: Number,
      required: false,
      default: 10
    }
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD");
    },
    searchHandler() {
      if (this.search.length == 0) {
        alert("검색어를 입력하세요!");
        this.$refs.search.focus();
        return;
      }

      this.boards = [];
      axios.get(`http://localhost:9999/pjt/api/board`).then(({ data }) => {
        data.forEach(obj => {
          if (obj.name.includes(this.search)) this.emps.push(obj);
        });
      });
    },
    gowrite() {
      this.$router.push("/jds/board/create");
    },
    nextPage() {
      this.pageNum += 1;
    },
    prevPage() {
      this.pageNum -= 1;
    }
  },
  computed: {
    pageCount() {
      let listLeng = this.boards.length,
        listSize = this.pageSize,
        page = Math.floor(listLeng / listSize);
      if (listLeng % listSize > 0) page += 1;

      /*
      아니면 page = Math.floor((listLeng - 1) / listSize) + 1;
      이런식으로 if 문 없이 고칠 수도 있다!
      */
      return page;
    },
    paginatedData() {
      const start = this.pageNum * this.pageSize,
        end = start + this.pageSize;
      console.log(start + ", " + end);
      return this.boards.slice(start, end);
    }
  }
};
</script>
