<script>
import { mapGetters } from "vuex";
import axios from "axios";
export default {
  data() {
    return {
      no: "",
      regtime: "",
      writer: "",
      title: "",
      content: ""
    };
  },
  computed: {
    ...mapGetters(["item"])
  },
  created() {
    this.$store.dispatch(
      "getBoard",
      `http://localhost:9999/vue/api/board/${this.$route.query.no}`
    );

    this.no = this.item.no;
    this.regtime = this.item.regtime;
    this.writer = this.item.writer;
    this.title = this.item.title;
    this.content = this.item.content;
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = "";
      !this.writer &&
        ((msg = "작성자를 입력해주세요"),
        (err = false),
        this.$refs.writer.focus());
      err &&
        !this.title &&
        ((msg = "제목 입력해주세요"), (err = false), this.$refs.title.focus());
      err &&
        !this.content &&
        ((msg = "내용 입력해주세요"),
        (err = false),
        this.$refs.content.focus());
      if (!err) alert(msg);
      else this.updateHandler();
    },
    updateHandler() {
      axios
        .put(`http://localhost:9999/pjt/api/board/${this.$route.query.no}`, {
          no: this.no,
          regtime: this.regtime,
          writer: this.writer,
          title: this.title,
          content: this.content
        })
        .then(({ data }) => {
          let msg = "수정 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "수정이 완료되었습니다.";
          }
          alert(msg);
          window.location.href = "/jds/resources/index.jsp";
        });
    }
  }
};
</script>
