import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    items: [],
    item: {}
  },
  getters: {
    items(state) {
      return state.items;
    },
    item(state) {
      return state.item;
    }
  },
  mutations: {
    setBoards(state, items) {
      state.items = items;
    },
    setBoard(state, item) {
      state.item = item;
    }
  },
  actions: {
    getBoards(context) {
      axios
        .get(`http://localhost:9999/pjt/api/board`)
        .then(({ data }) => {
          context.commit("setBoards", data);
        })
        .catch(() => {
          alert("에러가 발생했습니다.");
        })
        .finally(() => {
          console.log("actios: getBoards");
        });
    },
    getBoard(context, url) {
      axios.get(url).then(({ data }) => {
        context.commit("setBoard", data);
      });
    }
  },
  modules: {}
});
