import Vue from "vue";
import VueRouter from "vue-router";
import List from "@/views/List.vue";
import Create from "@/views/Create.vue";
import Read from "@/views/Read.vue";
import Readadmin from "@/views/Readadmin.vue";
import Update from "@/views/Update.vue";
import Delete from "@/views/Delete.vue";

Vue.use(VueRouter);

const routes = [
  { path: "/jds/board/list", name: "list", component: List },
  { path: "/jds/board/create", name: "create", component: Create },
  { path: "/jds/board/read/:no", name: "read", component: Read },
  { path: "/jds/board/readadmin/:no", name: "readadmin", component: Readadmin },
  { path: "/jds/board/update", name: "update", component: Update },
  { path: "/jds/board/delete", name: "delete", component: Delete }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
